:root {
  --background-color: #f7f7f7;
  --text-color: #4d4d4d;
  --facebook-color: #3b5998;
  --instagram-color: #C13584;
  --linkedin-color: #0e76a8;
}

@font-face {
  font-family: "Brandon";
  src: url(bd0b5482fffbc8bb950a0f64fccf4acc.eot); /* IE9*/
  src: url(bd0b5482fffbc8bb950a0f64fccf4acc.eot?#iefix) format("embedded-opentype"), 
  url(069c16e93e2cc85cd550be26d63c77d3.woff2) format("woff2"), 
  url(6e7fd87e67b85ca57ae990b4768fb879.woff) format("woff"), 
  url(57bc8125cd1371cd5941edb03cf4eb5f.ttf) format("truetype"), 
  url(e47ff54510f0ab791ea08cbeef1f176d.svg#Brandon%20Grot%20W01%20Light) format("svg"); /* iOS 4.1- */
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: Brandon;
}

body {
  font: 1rem 'Brandon', Helvetica, sans-serif;
  line-height: 1.4em;
  background: #ffffff;
  color: #4d4d4d;
  color: var(--text-color);
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -ms-font-smoothing: antialiased;
  font-smoothing: antialiased;
  font-weight: 300;
}

button,
input[type='checkbox'] {
  outline: none;
}

